<template>
    <div class="container">
        <h2 class="page-header"><i18n path="common.install"></i18n></h2>
        <p><i18n path="common.guide.useNpm"></i18n></p>
        <pre class="brush:bash">
            npm i v-uploader --save
        </pre>
        <p><i18n path="common.guide.include"></i18n></p>
        <pre class="brush:js">
            import Vue from 'vue'
            import vUploader from 'v-uploader';

            // v-uploader plugin global config
            const uploaderConfig = () => {
                return {
                    // file uploader server url
                    uploadFileUrl: 'http://xxx/upload/publicFileUpload',
                    // file delete server url
                    deleteFileUrl: 'http://xxx/upload/deleteUploadFile',
                    // set the way to show upload message(upload fail message)
                    showMessage: (vue, message) => {
                        //using v-dialogs to show message
                        vue.$vDialog.alert(message, null, {messageType: 'error'});
                    }
                }
            };

            // install plugin with params
            Vue.use(vUploader, uploaderConfig);
        </pre>
        <p><i18n path="upload.useDialog"></i18n></p>

        <h2 class="page-header"><i18n path="common.deploy"></i18n></h2>
        <p>template</p>
        <script type="syntaxhighlighter" class="brush:js">
        <![CDATA[
            <template>
              <!-- v-bind 'setting' data to config file uploader -->
              <!-- bind event 'done' to receive file uploaded info -->
              <v-uploader :setting="uploadConfig" @done="uploadDone" ></v-uploader>
            </template>
        ]]>
        </script>

        <p>script</p>
        <pre class="brush:js">
        export default {
            data(){
                return {
                    uploadConfig: {
                        multiple: false,
                        language: 'en'//default: 'cn'
                    }
                }
            },
            methods:{
                //receive file uploaded info
                //@type Array
                uploadDone(files){
                    if(files && Array.isArray(files) && files.length){
                        // do something...
                    }
                }
            }
        };
        </pre>

        <h2 class="page-header"><i18n path="common.example"></i18n> <small><i18n path="upload.demo.title"></i18n></small></h2>

        <h3><i18n path="upload.demo.single"></i18n></h3>
        <v-uploader :setting="singleConfig" @done="uploadDone" ></v-uploader>
        <v-uploader :setting="singleConfig" @done="uploadDone" ></v-uploader>
        <v-uploader :setting="singleConfig" @done="uploadDone" ></v-uploader>


        <h3><i18n path="upload.demo.multiple"></i18n> <small><i18n path="upload.demo.multipleSub"></i18n></small></h3>
        <v-uploader :setting="multipleConfig" @done="uploadDone" ></v-uploader>


        <h2 class="page-header"><i18n path="common.options"></i18n></h2>
        <table class="table table-bordered table-striped">
            <thead>
                <tr>
                    <th><i18n path="common.tbTitle.name"></i18n></th>
                    <th><i18n path="common.tbTitle.type"></i18n></th>
                    <th><i18n path="common.tbTitle.desc"></i18n></th>
                    <th><i18n path="common.tbTitle.required"></i18n></th>
                    <th><i18n path="common.tbTitle.default"></i18n></th>
                </tr>
            </thead>
            <tbody>
                <tr>
                    <td>multiple</td>
                    <td>boolean</td>
                    <td><i18n path="upload.option.multiple"></i18n></td>
                    <td>false</td>
                    <td>false</td>
                </tr>
                <tr>
                    <td>language</td>
                    <td>string</td>
                    <td>
                        <i18n path="upload.option.language"></i18n>
                        <ul>
                            <li><code>'cn'</code></li>
                            <li><code>'en'</code></li>
                        </ul>
                    </td>
                    <td>false</td>
                    <td>'cn'</td>
                </tr>
                <tr>
                    <td>fileSizeLimit</td>
                    <td>string</td>
                    <td><i18n path="upload.option.fileSizeLimit"></i18n></td>
                    <td>false</td>
                    <td>'5MB'</td>
                </tr>
                <tr>
                    <td>fileTypeExts</td>
                    <td>Array</td>
                    <td><i18n path="upload.option.fileTypeExts"></i18n></td>
                    <td>false</td>
                    <td>['jpeg', 'jpg', 'gif', 'png']</td>
                </tr>
                <tr>
                    <td>imageMaxHeight</td>
                    <td>number</td>
                    <td><i18n path="upload.option.imageMaxHeight"></i18n></td>
                    <td>false</td>
                    <td>0</td>
                </tr>
                <tr>
                    <td>imageMaxWidth</td>
                    <td>number</td>
                    <td><i18n path="upload.option.imageMaxWidth"></i18n></td>
                    <td>false</td>
                    <td>0</td>
                </tr>
                <tr>
                    <td>imageMinHeight</td>
                    <td>number</td>
                    <td><i18n path="upload.option.imageMinHeight"></i18n></td>
                    <td>false</td>
                    <td>0</td>
                </tr>
                <tr>
                    <td>imageMinWidth</td>
                    <td>number</td>
                    <td><i18n path="upload.option.imageMinWidth"></i18n></td>
                    <td>false</td>
                    <td>0</td>
                </tr>
            </tbody>
        </table>

        <h2 class="page-header"><i18n path="common.events"></i18n></h2>
        <table class="table table-bordered table-striped">
            <thead>
            <tr>
                <th><i18n path="common.tbTitle.param"></i18n></th>
                <th><i18n path="common.tbTitle.desc"></i18n></th>
                <th><i18n path="common.tbTitle.args"></i18n></th>
            </tr>
            </thead>
            <tbody>
            <tr>
                <td>done</td>
                <td><i18n path="upload.event.done"></i18n></td>
                <td>
                    <p><i18n path="upload.event.doneArgs"></i18n></p>
                    <p>type: Array</p>
                </td>
            </tr>
            </tbody>
        </table>

        <h2 class="page-header"><i18n path="upload.server.title"></i18n></h2>
        <h3><i18n path="upload.server.request.title"></i18n></h3>

        <table class="table table-bordered table-striped">
            <thead>
            <tr>
                <th><i18n path="common.tbTitle.param"></i18n></th>
                <th><i18n path="common.tbTitle.desc"></i18n></th>
            </tr>
            </thead>
            <tbody>
            <tr>
                <td>uploadFileObj</td>
                <td><i18n path="upload.server.request.uploadFileObj"></i18n></td>
            </tr>
            <tr>
                <td>fileSizeLimit</td>
                <td><i18n path="upload.server.request.fileSizeLimit"></i18n></td>
            </tr>
            <tr>
                <td>fileTypeExts</td>
                <td><i18n path="upload.server.request.fileTypeExts"></i18n></td>
            </tr>
            </tbody>
        </table>
        <h3><i18n path="upload.server.response.title"></i18n></h3>
        <p><i18n path="upload.server.response.desc"></i18n></p>
        <table class="table table-bordered table-striped">
            <thead>
            <tr>
                <th><i18n path="common.tbTitle.prop"></i18n></th>
                <th><i18n path="common.tbTitle.type"></i18n></th>
                <th><i18n path="common.tbTitle.desc"></i18n></th>
                <th><i18n path="common.tbTitle.required"></i18n></th>
            </tr>
            </thead>
            <tbody>
            <tr>
                <td>success</td>
                <td>boolean</td>
                <td><i18n path="upload.server.response.success"></i18n></td>
                <td>true</td>
            </tr>
            <tr>
                <td>error</td>
                <td>string</td>
                <td><i18n path="upload.server.response.error"></i18n></td>
                <td>true</td>
            </tr>
            <tr>
                <td>url</td>
                <td>string</td>
                <td><i18n path="upload.server.response.url"></i18n></td>
                <td>true</td>
            </tr>
            </tbody>
        </table>
        <h4><i18n path="upload.server.response.example"></i18n></h4>
        <pre class="brush:js">
        {
            "success": true,//required field
            "error": null,  //when file upload fail, this field will set the fail reason
            "url": "https://xx/someimg.jpg"//a full path of image to show in thumbnail
        }
        </pre>
    </div>
</template>

<script>
    export default {
        name: "Demo",
        data(){
            let arr = [];
            for (let i = 0; i < 108; i++) {
                arr.push(i+1);
            }
            return {
                singleConfig: {
                    language: 'en'
                },
                multipleConfig: {
                    multiple: true,
                    language: 'en'
                },
                singleCode: false,
                multipleCode: false
            };
        },
        methods: {
            uploadDone(files){
                if(files && Array.isArray(files) && files.length){
                    console.log(files);
                }
            }
        },
        mounted(){
            SyntaxHighlighter.highlight();
        }
    }
</script>

<style lang="scss" scoped>
</style>


